<template>
  <div class="group-chat-index">
    <mt-search class="search-box" v-model="value"></mt-search>
    <template v-for="(item, i) in list">
      <group-chat-item :item="item" :key="i" @toChat="handleToChat(item)"></group-chat-item>
    </template>
    <div class="goup-chat-num">{{list.length}}个群聊</div>
  </div>
</template>
<script>
import GroupChatItem from '@/components/GroupChatItem'
export default {
  name: 'ChatList',
  components: { GroupChatItem },
  data () {
    const user = localStorage.getItem('user')
    return {
      user: (user && JSON.parse(user)) || {},
      value: null,
      list: [],
      dataList: []
    }
  },
  watch: {
    value (val) {
      // this.list = this.dataList.filter(v => v.chatName.includes(val))
      this.getGroups(val)
    }
  },
  created () {
    this.getGroups('')
  },
  methods: {
    getGroups (condition) {
      this.$socket.getGroups(condition, this.user.id, (res) => {
        this.list = res.groups
      })
    },
    handleToChat (item) {
      this.$router.push('/groupChat')
      localStorage.setItem('chatItem', JSON.stringify(item))
    }
  }
}
</script>
<style lang="less">
  .group-chat-index {
    background: #fff;
    .goup-chat-num {
      background: #F6F7F8;
      text-align: center;
      font-size: 14px;
      color: #808080;
      padding: 17px 0;
    }
  }
</style>
